//=== COLOURS ===//
$dark-grey: #222222;
$text-color: #383838;
$mid-grey: #707070;
$white: #ffffff;
//===BACKGROUND-COLOURS===//
$dark-blue: #0B3A63;
$deep-yellow: #F2A61E;
$light-grey: #EFEFEF;
//===GRADIENT-COLOURS===//

//===SIZING===//
$page-height: 100vh;
$position-abs: absolute;
$position-rel: relative;
$container-width: 100%;
$center: 0 auto;

$content-wide-col: 90%;
$content-col: 70%;
$main-content-col-1-width: 72%;
$main-content-col-2-width: 25%;
$grid-container: 110%;

$content-col-fifth: 20%;

$main-content-col-half-width: 49%;
$float-left: left;
$float-right: right;

//===SPACING===//
$containerPadding: 20px 0px;

//===FONTS===//
$headline-font: 'Courier New', Courier, sans-serif;
$text-font: Helvetica, Verdana, sans-serif;

//===WEIGHTS===//
$thin: 100;

//===COMPONENTS===//
@mixin col-left {
    width: $main-content-col-half-width;
    float: $float-left;
}

@mixin col-right {
    width: $main-content-col-half-width;
    float: $float-right;
}

@mixin sectionContainer {
    background: $light-grey;
    color: $text-color;
    width: $container-width;
    margin: $center;
    padding: $containerPadding;
    display: flex;
    clear:both;
}

@mixin backgroundImage {
    background-image: url("../Assets/images/ARGheader_bg.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

@mixin pageContent {
    width: $content-col;
    margin: $center;
    clear: both;
}

@mixin title {
    text-transform: uppercase;
    weight: $thin;
}

@mixin grid {
    height: 100px;
    width: $content-col-fifth;
    float: $float-left;
    border-right: 1px solid $white;
    margin-top: 40px;
    padding: 10px;
}

//===OVERRIDES===//


