@import "global.scss";


.recommendContainer {
    @include sectionContainer;
    @include backgroundImage;
}

.col-left{
    @include col-left;
    clear: both;
    text-align: left;
}

.pageContent {
    @include pageContent;
}

.recQuote {
    width: $main-content-col-2-width;
    color: $deep-yellow;
    font-size: 20vw;
    float: $float-left;
}

.recCon {
    width: $main-content-col-1-width;
    float: $float-right;
}

.recommendText {
    font-size: 40px;
    font-weight: $thin;
    color: $white;
    text-transform: uppercase;
}

.authorMeta {
    color: $deep-yellow;
}


