@import "global.scss";

.navContainer {
    @include sectionContainer;
    position: relative;
    top: 20px;
    background: $dark-blue;
    opacity: .9;
    padding: 30px 0px;
}
.pageContent {
    @include pageContent;
}

ul {
    display: flex;
    justify-content: space-between;
}

ul.nav {
    font-family: courier;
}

.links {
    color: $white;
    display: inline-block;
}

.separator {
    color: $deep-yellow;
    display: inline-block;
}