@import "global.scss";

header{
    @include sectionContainer;
    @include backgroundImage;
    font-family: courier;
}

header p {
    margin: 0px;
    padding: 0px;
}

.headerBanner {
    width: $content-wide-col;
    display: inline-block;
    clear: both;
}

.headerLogo {
    @include col-left;
    text-align: left;
    font-weight: bold;
    font-size: 30px;
    color: $deep-yellow;
}

.yellow {
    color: $deep-yellow;
}

.headerContact {
    @include col-right;
    color: $white;
    text-align: right;
    display: inline;
    font-size: 20px;
}

.headerIcon {
    width: 20px;
    color: $deep-yellow;
}

.pageContent {
    @include pageContent;
}

.headerHeadline {
    text-align: center;
    margin: auto;
    padding: 20vh 0 10vh 0;
    font-size: 80px;
    color: $white;
}

h1.headerHeadline {
    font-weight: $thin;
}

.headerButton {
    background-color: $deep-yellow;
    border-radius: 10px;
    margin: 0 0 50px 0;
}