@import "global.scss";

.servicesContainer {
    @include sectionContainer;
    @include backgroundImage;
}

.containingServices {
    @include sectionContainer;
}

.pageContent {
    @include pageContent;
}

.servicesContainer > .pageContent {
    clear:both;
}

.titleWhite {
    @include title;
    color:$white;
}

.servicesContainer > .pageContent > p {
    color: $white;
}

.gridContainer {
    width: $grid-container;
    margin: $center;
}

.gridItem {
    @include grid;
    color: $white;
}

.gridItem:nth-last-child(2) {
    border: none;
}

.servicesIcon {
    color: $deep-yellow;
    width: 20%;
}