@import "global.scss";

.aboutContainer {
    @include sectionContainer;
}

.col-left{
    @include col-left;
}
.pageContent {
    @include pageContent;
}

.aboutCopy {
    width: $content-col;
    text-align: center;
    margin: $center;
}

ul.aboutList{
    display: block;
    list-style-type: disc;
    color: $deep-yellow;
}

ul.aboutList li{
    color: $mid-grey;
    text-align: left;
}

ul.aboutList li p.title {
    font-weight: bold;
    font-size: 14px;
}

 .listContent {
    font-size: 14px;
}

.col-right{
    @include col-right;
}

.titleblue {
    color: $dark-blue;
}

.titleyellow {
    color: $deep-yellow;
}
.title {
    @include title;
}

